@import url(https://fonts.googleapis.com/css?family=Quantico:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anton);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
html {
	width: 100%;
	height: 100%;
	margin: 0;
}
body {
	overflow-y: hidden;
	position: fixed;
	width: 100%;
	height: 100%;
	font-family: 'Anton', sans-serif;
	margin: 0;
	background: -webkit-gradient(linear, right top, left top, from(rgb(80, 221, 237)), to(rgb(235, 78, 223)));
	background: linear-gradient(to left, rgb(80, 221, 237), rgb(235, 78, 223));
}

.Tilt {
	background: -webkit-gradient(linear, left top, right top, from(rgb(80, 221, 237)), to(rgb(235, 78, 223)));
	background: linear-gradient(to right, rgb(80, 221, 237), rgb(235, 78, 223));
}

.logo {
	position: absolute;
	top: 20px;
	right: 500px;
	bottom: 1000px;
	left: 20px;
}

.inputbuttondiv {
	background-color: hsl(2, 57%, 40%);
	background-image: repeating-linear-gradient(
			transparent,
			transparent 50px,
			rgba(0, 0, 0, .4) 50px,
			rgba(0, 0, 0, .4) 53px,
			transparent 53px,
			transparent 63px,
			rgba(0, 0, 0, .4) 63px,
			rgba(0, 0, 0, .4) 66px,
			transparent 66px,
			transparent 116px,
			rgba(0, 0, 0, .5) 116px,
			rgba(0, 0, 0, .5) 166px,
			rgba(255, 255, 255, .2) 166px,
			rgba(255, 255, 255, .2) 169px,
			rgba(0, 0, 0, .5) 169px,
			rgba(0, 0, 0, .5) 179px,
			rgba(255, 255, 255, .2) 179px,
			rgba(255, 255, 255, .2) 182px,
			rgba(0, 0, 0, .5) 182px,
			rgba(0, 0, 0, .5) 232px,
			transparent 232px
		),
		repeating-linear-gradient(
			270deg,
			transparent,
			transparent 50px,
			rgba(0, 0, 0, .4) 50px,
			rgba(0, 0, 0, .4) 53px,
			transparent 53px,
			transparent 63px,
			rgba(0, 0, 0, .4) 63px,
			rgba(0, 0, 0, .4) 66px,
			transparent 66px,
			transparent 116px,
			rgba(0, 0, 0, .5) 116px,
			rgba(0, 0, 0, .5) 166px,
			rgba(255, 255, 255, .2) 166px,
			rgba(255, 255, 255, .2) 169px,
			rgba(0, 0, 0, .5) 169px,
			rgba(0, 0, 0, .5) 179px,
			rgba(255, 255, 255, .2) 179px,
			rgba(255, 255, 255, .2) 182px,
			rgba(0, 0, 0, .5) 182px,
			rgba(0, 0, 0, .5) 232px,
			transparent 232px
		),
		repeating-linear-gradient(
			125deg,
			transparent,
			transparent 2px,
			rgba(0, 0, 0, .2) 2px,
			rgba(0, 0, 0, .2) 3px,
			transparent 3px,
			transparent 5px,
			rgba(0, 0, 0, .2) 5px
		);
}

.App {
	text-align: center;
}

.particles {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
}

.bounding-box {
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}

.imgcontainer {
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.font1 {
	font-family: 'Pacifico', cursive;
}

.font2 {
	font-family: 'Anton', sans-serif;
}

.font3 {
	font-family: 'Quantico', sans-serif;
}

